:root {
    --bs-border-color: var(--tc-system-stroke) !important;
    --tc-theme-primary: var(--tc-system-orange);
    --bs-primary-rgb: 255, 101, 46;
    --bs-dark-rgb: 41, 41, 41;

    .bg-secondary-2 {
        background-color: #fcf8f3;
    }
}

// background colors
.bg-light {
    background-color: var(--tc-bg-light) !important;
}
.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}

// variables end

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
